.header {
    min-height: 100vh;
    height: auto;
    display: flex;
    flex-direction: column;
    position: relative;
    overflow: hidden;
    padding-bottom: 100px;
    background-image: radial-gradient(fade(@color-4, 85%) 5%, darken(@color-4, 15%) 85%), url('../img/header-bg.jpg');
    background-size: cover;
    background-position: center bottom;
    &::before,
    &::after {
        content: '';
        position: absolute;
        left: -100%;
        right: -100%;
        height: 1000px;
        bottom: -1000px;
        background-color: lighten(@color-1, 12%);
        z-index: 200;
    }
    &::before {
        transform: rotate(6deg);
    }
    &::after {
        transform: rotate(-6deg);
    }
    /* header end */
    &-navbar {
        padding: 15px;
        text-align: left;
    }
    .container {
        position: relative;
        z-index: 100;
        padding-top: 25px;
        &::after {
            content: '';
            position: absolute;
            left: -100%;
            right: -100%;
            bottom: -300%;
            top: 0;
            background-image: linear-gradient(to bottom, fade(@color-4, 25%) 5%, darken(@color-4, 15%) 85%);
            z-index: -1;
        }
    }
    &-target {
        .col-grow();
        color: #ffffff;
        text-align: center;
        p {
            font-size: 1.2em;
            font-weight: bold;
        }
    }
    .title(#fff,
        {
        overflow: hidden;
        animation-name: anim-title-container;
        margin-top: auto;
        margin-bottom: auto;
        &::before {
            // verticle bar
            content: '';
            position: absolute;
            z-index: 2;
            top: 0;
            left: 0;
            height: 100%;
            width: 3px;
            background-color: #ffffff;
            animation: anim-pulse 2s 0.3s both alternate;
        }
    }
    );
    /* animation */
    .title-container,
    &-title,
    &-text {
        animation-duration: 0.5s;
        animation-delay: 2.5s;
        animation-fill-mode: backwards;
    }
    &-title,
    &-text {
        animation-name: anim-title-content;
        line-height: 1.4;
    }
    /* animation end */
    &-title {
        font-size: 50px;
        text-transform: uppercase;
    }
    &-text {
        font-size: 20px;
    }
    &-icon {
        position: absolute;
        bottom: 50px;
        left: 50%;
        font-size: 30px;
        color: #ffffff;
        margin-left: -19px / 2;
        z-index: 200;
        i {
            cursor: pointer;
        }
    }
}