@content: {};

.title(@color: @color-text, @content: @content) {
    .title-container {
        .container();
        border-left: 3px solid @color;
        color: @color;
        padding-left: 30px;
        padding-right: 30px;
        text-align: left;
        line-height: 1;
        margin-bottom: 1em * 3;
        position: relative;
        z-index: 10;
        @content();
        &-shadow {
            height: 1.1em;
            padding-left: 30px;
            position: absolute;
            top: -1em / 2;
            left: 0;
            right: 0;
            font-size: 5em;
            opacity: 0.05;
            font-weight: bold;
            line-height: 1;
            overflow: hidden;
            user-select: none;
            z-index: -1;
        }
    }
}

.bxsh(@color: @color-1) {
    box-shadow: 0px 0px 25px 0px fade(@color, 50%);
}

.card(@color: @color-1, @padding: 25px) {
    padding: @padding;
    border-radius: 5px;
    .bxsh(@color);
}

.icon-create(@var: '?', @content: @content) {
    &::before {
        font-family: 'Icon';
        content: @var;
        font-weight: 900;
        line-height: 1;
        @content();
    }
}